import React, {Component} from "react";
import {
    Button,
    Checkbox,
    Dropdown,
    Form,
    FormGroup,
    TextArea,
    TextInput,
    ToastNotification
} from "carbon-components-react";
import './form-comp.scss';

interface FormCompProps {
    confFun: Function,
    initialData: any,
    status: number
}

class FormComp extends Component<FormCompProps, {}> {

    rankItems = ['Cadet', 'Lance Corporal', 'Corporal', 'Sergeant', 'Company Quartermaster Sergeant',
        'Junior Under Officer', 'Senior Under Officer'];

    departments = ['Aerospace Engineering', 'Biosciences and Bioengineering', 'Chemical Engineering', 'Chemistry',
        'Civil Engineering', 'Computer Science & Engineering', 'Earth Sciences', 'Electrical Engineering',
        'Energy Science and Engineering', 'Environmental Science and Engineering (ESED)',
        'Humanities & Social Sciences', 'IDC School of Design', 'Mathematics', 'Mechanical Engineering',
        'Metallurgical Engineering & Materials Science', 'Physics']

    years = ['2015', '2016', '2017', '2018', '2019', '2020', '2021'];

    formData = {
        name: '',
        rank: '',
        dept: '',
        mob: '',
        email: '',
        presAddr: '',
        addrSame: false,
        permAddr: '',
        trStart: '',
        trEnd: '',
        cert: '',
        job: ''
    }

    status = -1;
    notifContent = '';
    notifTimeout = setTimeout(() => {
    }, 0);

    goToConfirmation() {
        if (this.status !== 0) return;
        if (this.formData.name === '') {
            this.notifContent = 'Enter a valid name.';
        } else if (this.formData.rank === '') {
            this.notifContent = 'Select a rank.';
        } else if (this.formData.dept === '') {
            this.notifContent = 'Select a department.';
        } else if (!/^[0-9]{10}$/.test(this.formData.mob)) {
            this.notifContent = 'Enter a valid 10-digit mobile number without the country code.';
        } else if (!/^[^\s@]+@[^\s@]+$/.test(this.formData.email)) {
            this.notifContent = 'Enter a valid email addresss.';
        } else if (this.formData.presAddr === '') {
            this.notifContent = 'Present Address field is required.';
        } else if (!this.formData.addrSame && this.formData.permAddr === '') {
            this.notifContent = 'Permanent Address field is required.';
        } else if (this.formData.trStart === '' || this.formData.trEnd === '') {
            this.notifContent = 'Enter a valid training period.';
        } else if (this.formData.cert === '') {
            this.notifContent = 'Select your NCC certificates.';
        } else if (this.formData.job === '') {
            this.notifContent = 'Enter your present job.';
        }

        if (this.notifContent !== '') {
            clearTimeout(this.notifTimeout);
            this.notifTimeout = setTimeout(() => {
                this.notifContent = '';
                this.forceUpdate();
            }, 3000);
            this.forceUpdate();
        } else {
            clearTimeout(this.notifTimeout);
            this.props.confFun(this.formData);
            this.status = 1;
        }
    }

    render() {
        if (this.status === -1) {
            if (this.props.status === 2) this.formData = this.props.initialData;
            this.status = 0;
        }
        return <Form>
            <FormGroup legendText=''>
                <TextInput id='name' labelText='Name'
                           onChange={(e) => this.formData.name = e.target.value}
                           defaultValue={this.formData.name}/>
            </FormGroup>
            <FormGroup legendText=''>
                <Dropdown id='rank' items={this.rankItems} label='Select your rank'
                          titleText='Rank at NCC'
                          onChange={(e) => this.formData.rank = (e.selectedItem as string)}
                          initialSelectedItem={this.formData.rank}/>
            </FormGroup>
            <FormGroup legendText=''>
                <Dropdown id='dept' items={this.departments} label={'Select your department'}
                          titleText='Academic Department'
                          onChange={(e) => this.formData.dept = (e.selectedItem as string)}
                          initialSelectedItem={this.formData.dept}/>
            </FormGroup>
            <FormGroup legendText=''>
                <TextInput id='num' minLength={10} maxLength={10} labelText='Mobile Number'
                           type='number' invalidText="Enter a valid mobile number without the country code."
                           onChange={(e) => this.formData.mob = e.target.value}
                           defaultValue={this.formData.mob}/>
            </FormGroup>
            <FormGroup legendText=''>
                <TextInput id='email' labelText='Email Address' type='email' invalidText='Enter a vaild email address.'
                           onChange={(e) => this.formData.email = e.target.value}
                           defaultValue={this.formData.email}/>
            </FormGroup>
            <FormGroup legendText=''>
                <TextArea labelText='Present Address' rows={3}
                          onChange={(e) => this.formData.presAddr = e.target.value}
                          defaultValue={this.formData.presAddr}/>
            </FormGroup>
            <FormGroup legendText=''>
                <Checkbox id='addr-check' labelText='Permanent Address is the same as the Present Address'
                          onChange={(e: boolean) => {
                              this.formData.addrSame = e;
                              this.forceUpdate();
                          }} defaultChecked={this.formData.addrSame}/>
            </FormGroup>
            {this.formData.addrSame ? null : <FormGroup legendText=''>
                <TextArea labelText='Permanent Address' rows={3}
                          onChange={(e) => this.formData.permAddr = e.target.value}
                          defaultValue={this.formData.permAddr}/>
            </FormGroup>}
            <FormGroup legendText='NCC Training Period'>
                <Dropdown type='inline' id='start-year' items={this.years.slice(0, -1)} label='Select Year'
                          titleText='From' onChange={e => {
                    this.formData.trStart = e.selectedItem as string;
                    this.forceUpdate();
                }
                } initialSelectedItem={this.formData.trStart}/>
                &nbsp;&nbsp;&nbsp;
                {this.formData.trStart === '' ? null :
                    <Dropdown type='inline' id='end-year' items={(() => {
                        const index = this.years.indexOf(this.formData.trStart);
                        return this.years.slice(index + 1, index + 4);
                    })()} label='Select Year'
                              titleText='To' onChange={e => this.formData.trEnd = e.selectedItem as string}
                              initialSelectedItem={this.formData.trEnd}/>}
            </FormGroup>
            <FormGroup legendText=''>
                <Dropdown id='cert' items={['None', 'B Certificate', 'C Certificate']} label='Select certificates'
                          titleText='Certificates' onChange={e => this.formData.cert = e.selectedItem as string}
                          initialSelectedItem={this.formData.cert}/>
            </FormGroup>
            <FormGroup legendText=''>
                <TextInput id='job' labelText='Present Job' onChange={e => this.formData.job = e.target.value}
                           defaultValue={this.formData.job}/>
            </FormGroup>
            <FormGroup legendText='' style={{textAlign: 'right'}}>
                <Button type='submit' size='small' onClick={(e) => {
                    e.preventDefault();
                    this.goToConfirmation();
                }} disabled={this.notifContent !== ''}>Submit</Button>
            </FormGroup>
            <div style={{position: 'fixed', bottom: '20px', right: '20px'}}>
                {this.notifContent === '' ? null :
                    <ToastNotification hideCloseButton={true} title={this.notifContent}/>}
            </div>
        </Form>;
    }
}

export default FormComp;