import React, {Component} from "react";
import {
    Button,
    FormGroup,
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow
} from "carbon-components-react";

interface ConfirmProps {
    formData: any,
    editForm: Function,
    confirmSub: Function
}

class Confirm extends Component<ConfirmProps, {}> {
    state = {};

    fields = {
        name: 'Name',
        rank: 'Rank at NCC',
        dept: 'Academic Department',
        mob: 'Mobile Number',
        email: 'Email Address',
        presAddr: 'Present Address',
        permAddr: 'Permanent Address',
        trPeriod: 'Training Period',
        cert: 'Certificates',
        job: 'Present Job'
    }

    formData = {
        name: 'Ayush Jangir',
        rank: 'Corporal',
        dept: 'Computer Science & Engineering',
        mob: '8696923282',
        email: 'ayush.jangir.0133@gmail.com',
        presAddr: 'Antroli',
        addrSame: true,
        permAddr: 'Antroli2',
        trStart: '2019',
        trEnd: '2021',
        cert: 'None',
        job: 'Student'
    }

    columns = ['Field', 'Your Entry'];

    render() {
        return <div>
            <p style={{marginBottom: '2em'}}>
                The data you have entered is as follows. Please confirm or edit the same.
            </p>
            <Table size='normal'>
                <TableHead>
                    <TableRow>
                        {this.columns.map(col => (
                            <TableHeader key={col}>{col}</TableHeader>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {Object.keys(this.fields).map(key => {
                        const t = this;
                        return (
                            <TableRow key={key}>
                                <TableCell
                                    style={{fontWeight: 'bold'}}>{this.fields[key as keyof typeof t.fields]}</TableCell>
                                <TableCell>{key === 'permAddr' ? (
                                    (this.props.formData.addrSame ? this.props.formData.presAddr : this.props.formData.permAddr)
                                ) : key === 'trPeriod' ? (this.props.formData.trStart + ' to ' + this.props.formData.trEnd) :
                                    this.props.formData[key as keyof typeof t.props.formData]}</TableCell>
                            </TableRow>
                        );
                    })}
                </TableBody>
            </Table>
            <FormGroup legendText='' style={{textAlign: 'right', marginTop: '1em'}}>
                <Button kind='secondary' size='small'onClick={this.props.editForm}>Edit</Button>
                &nbsp;
                <Button type='submit' size='small'onClick={e => {
                    e.preventDefault();
                    this.props.confirmSub();
                }}>Confirm</Button>
            </FormGroup>
        </div>;
    }
}

export default Confirm;