import React from 'react';
import './App.css';
import logo from './ncclogo.svg';
import logo2 from './ncciitb.svg';
import {Button, FormGroup, Header} from "carbon-components-react";
import Confirm from "./components/confirm/confirm";
import FormComp from "./components/form/form-comp";

class App extends React.Component {

    state = {
        status: 0,
        formData: {}
    }

    submitForm() {
        if (this.state.status !== 1 && this.state.status !== 5) return;
        const url = 'https://ncciitb.eastus2.cloudapp.azure.com/register.php';
        const newState = JSON.parse(JSON.stringify(this.state));
        newState.status = 3;
        this.setState(newState);

        fetch(url, {
            method: 'POST',
            body: JSON.stringify(this.state.formData)
        }).then(async response => {
            const data = await response.json();
            if (data.status === 1) {
                this.setState({status: 4, formData: this.state.formData});
            }
        }).catch(() => {
            console.error('Error');
            this.setState({status: 5, formData: this.state.formData});
        });
    }

    render() {
        return (
            <div className='App'>
                <Header style={{position: 'sticky', top: 0, height: '6em'}} aria-label=''>
                    <img alt='NCC Logo' src={logo}
                         style={{width: '45px', position: 'fixed', left: '20px', top: '10px'}}/>
                    <div style={{
                        margin: '0 auto',
                        textAlign: 'center',
                        color: '#ffffff',
                        lineHeight: '1.5em',
                        fontSize: '1.5em'
                    }}>
                        2 MER NCC<br/><span style={{fontWeight: 'bold'}}>Alumni Registration</span>
                    </div>
                    <img alt='NCC Logo' src={logo2}
                         style={{width: '90px', position: 'fixed', right: '10px', top: '0'}}/>
                </Header>
                <div className="form" style={{padding: '30px 0'}}>
                    {this.state.status === 0 || this.state.status === 2 ? <FormComp confFun={(data: any) => {
                            this.setState({status: 1, formData: data});
                        }} initialData={this.state.formData} status={this.state.status}/>
                        : this.state.status === 1 ? <Confirm formData={this.state.formData} editForm={() => {
                            const newState = JSON.parse(JSON.stringify(this.state));
                            newState.status = 2;
                            this.setState(newState);
                        }} confirmSub={() => {
                            this.submitForm();
                        }}/> : this.state.status === 3 ?
                            <div style={{fontSize: '1.1em', textAlign: 'center', marginTop: '50px'}}>
                                Submitting...
                            </div>
                            : this.state.status === 4 ?
                                <div style={{fontSize: '1.1em', textAlign: 'center', marginTop: '50px'}}>
                                    {/* @ts-ignore */}
                                    You have been registered successfully. You can now close the tab.
                                </div>
                                :
                                <div style={{fontSize: '1.1em', textAlign: 'center', marginTop: '50px'}}>
                                    <span style={{color: '#880000'}}>Registration failed.</span> Kindly check your
                                    internet connection and the validity of the data you have entered.
                                    <FormGroup legendText='' style={{marginTop: '25px'}}>
                                        <Button kind='secondary' size='small' onClick={() => {
                                            this.setState({status: 2, formData: this.state.formData});
                                        }}>Edit the Form</Button>
                                        &nbsp;
                                        <Button size='small' onClick={() => {
                                            this.setState({status: 3, formData: this.state.formData});
                                            this.submitForm();
                                        }}>Retry Submission</Button>
                                    </FormGroup>
                                </div>
                    }
                </div>
            </div>
        );
    }

}

export default App;
